import { createRouter, createWebHashHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import BlogList from "../components/BlogList.vue"; // 경로 수정: .vue 확장자 추가
import BlogDetail from "../components/BlogDetail.vue";
import Author from "../components/Author.vue";
import Comment from "../components/Comment.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/BlogList",
    name: "BlogList",
    component: BlogList,
    props: (route) => ({ BlogW: route.query.BlogW || "defaultWidth" }), // 쿼리 파라미터를 프롭스로 전달
  },
  {
    path: "/BlogDetail/:id",
    name: "BlogDetail",
    component: BlogDetail,
    children: [
      {
        path: "author", //상대경로 /BlogDetail/0/author
        component: Author, //import해온 컴포넌트
      },
      {
        path: "comment",
        component: Comment,
      },
    ],
  },
];

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
});

export default router;
